.btn-more {
  display: flex;
  transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out, color 0.3s ease-in-out, border 0.3s ease-in-out, text-shadow 0.3s ease-in-out;
}

.btn-more:hover .arrow {
  transform: translateX(1.0416666667vw);
}

@media (max-width: 900px) {
  .btn-more:hover .arrow {
    transform: translateX(0);
  }
}
.btn-more:active {
  transform: translate(1px, 1px);
}

.btn-more .more {
  display: flex;
  padding: 0.4vw 2vw;
  padding-right: 1.7vw;
  border-radius: 2vw;
  cursor: pointer;
  border: 1px solid #c3b09d;
  color: #c3b09d;
  transition: all 0.35s ease-in-out;
}

@media (max-width: 900px) {
  .btn-more .more {
    padding: 1.640625vw 7vw;
    border-radius: 30px;
  }
}
