  font-family: "Cormorant Infant", serif;
  font-size: 0.95vw;
}

@media (max-width: 900px) {
  .btn-more div {
    font-size: 3.5vw;
  }
}
.btn-more .arrow {
  width: 0.9895833333vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 0.3vw;
  margin-top: -0.2vw;
  transition: all 0.35s ease-in-out;
}

@media (max-width: 900px) {
  .btn-more .arrow {
    width: 3.75vw;
    margin-left: 1.3020833333vw;
  }
}
